import '@/filter/filter'
import {
  wallet } from "@/utils/wallet";
export default {
  data() {
    return {
      address: "",
    };
  },
  async mounted() {
  //  this.connect()
  },
  methods: {
    async connect() {
      if (wallet.isConnected()) {
        const address = await wallet.selectedAddress()
        this.address = address
      } else {
        this.address = await wallet.connect();
      }
    }
  }
}

